<template>
  <div class="books main">
    <el-button @click="save()">Save</el-button>
    <el-button @click="onShareClick()">Share</el-button>
    <!-- <ul>
      <li
        v-for="(item, index) in listDatas"
        :key="index"
        style="list-style:none"
      >
        <card :item="item" :index="index" />
      </li>]
    </ul>-->

    <div class="table-responsive-vertical shadow-z-1">
      <!-- Table starts here -->
      <table id="table" class="table table-hover table-mc-light-blue">
        <thead>
          <tr>
            <th>Index</th>
            <th>Thumbnail</th>
            <th>Name</th>
            <th>Qty</th>
            <th>Variations</th>
          </tr>
        </thead>
        <tbody>
          <card v-for="(item, index) in listDatas" :key="index" :item="item" :index="index" />
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import card from "./card.vue";
import datas from "@/output.js";
import Codes from "../../utils/codes";

// const datas = []

export default {
  name: "Books",
  components: {
    card
  },
  data() {
    return {
      datas
    };
  },
  computed: {
    bookFile: {
      get() {
        return this.$store.state.bookFile;
      },
      set(value) {
        this.$store.commit("UPDATE_BOOKFILE", value);
      }
    },
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      }
    },
    listDatas() {
      return this.$store.state.listDatas;
    }
  },
  mounted() {
    const hash = this.$route.params.hash;
    if (hash) {
      this.$store.commit("READ_STATUS", hash);
    } else {
      this.$store.commit("LOAD_STATUS");
    }
  },
  methods: {
    save() {
      this.$store.commit(
        "SAVE_STATUS",
        Codes.generateHash(this.status.join(""))
      );
    },
    onShareClick() {
      Codes.generateURL(this.status.join(""), url => {
        window.prompt("Copy to clipboard: Ctrl+C", `${url}`);
      });
    }
  }
};
</script>

